var sideNav = {
	openNav: function(){
		document.getElementById("mySidenav").style.width = "250px";
		document.getElementById("mySidenav").style.borderLeft = "";
   		document.body.style.backgroundColor = "rgba(0,0,0,0.4)";
	},
	closeNav: function(){
		document.getElementById("mySidenav").style.width = "0";
   		document.getElementById("mySidenav").style.borderLeft = "";
    	document.body.style.backgroundColor = "white";
	},
	flag: false,
	showDopSideNav: function(){
		document.querySelector(".setup_sidenav").onclick = function(e){
			e.preventDefault()
			if(!sideNav.flag){
				this.style.backgroundPosition = "90px 6px"
				this.style.color = "#ec6608"
				document.querySelector(".dop_menu_side").style.height = "175px"
				setTimeout(function(){document.querySelector(".dop_menu_side > ul").style.display = "block"},600)
				sideNav.flag = !sideNav.flag
			}else{
				this.style.backgroundPosition = "90px -22px"
				document.querySelector(".dop_menu_side").style.height = "0px"
				document.querySelector(".dop_menu_side > ul").style.display = "none"
				sideNav.flag = !sideNav.flag
				this.style.color = "#a8aaab"
			}
		}
	}
}

module.exports = sideNav;